.route-nav-enter {
  opacity: 0;
  transform: scale(0.9);
}

.route-nav-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}

.route-nav-exit {
  opacity: 1;
}

.route-nav-exit-active {
  opacity: 0;
  transform: scale(0.9);
  /* transition: opacity 300ms, transform 300ms; */
}
